import React, { useEffect } from 'react'

import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment';


import { useForm } from '../../hooks/useForm'
import { addNewFactura } from '../../actions/factura';
import { getSocios } from '../../actions/socio';

export const FormFactura = () => {

    const dispatch = useDispatch()

    const {socios} = useSelector(state=> state.socio)

    

    const [ facturaValues, handleInputChange ] = useForm({
        cedula: '',
        //fecha: moment().format("YY MMM Do"),
        //fecha: moment().format('l'),
        fecha: '',
       
        descripcion: '',
        monto: '',
       
      })
    
      const { cedula, fecha, descripcion, monto } = facturaValues
    
       
    
      const handleSumit = (e) => {
    
        e.preventDefault();
    
       dispatch( addNewFactura( facturaValues ) )
       
       

      }


      useEffect(() => {
        let isApiSubscribed =true;
        if(isApiSubscribed){
            dispatch(getSocios());
        }
         return () => {
            isApiSubscribed = false;
         }
       }, [dispatch])
/*
       
*/
      

    

  return (
    <div>

        <form onSubmit={handleSumit} className='formSocio'>
            <div>
               
            <div className="mb-3">
                     <select 
                        value={cedula}   
                        onChange={handleInputChange}  
                        name='cedula'
                        id="disabledSelect" 
                        className="form-select"  
                        
                        >
                        <option>Selecciona el nombre</option>
                        {socios.map(soc=>{
                            
                            return(
                                <option 
                                    key={soc._id} 
                                    value={soc.cedula}                              
                                >   
                                    {soc.nombre} {soc.apellidos}
                                </option>
                            )
                        })}
                    </select>
                </div>

               
                <input
                    type='date'
                    name='fecha'
                    className='form-control mt-3'
                    placeholder='Fecha'
                    autoComplete='off'
                    value={fecha}
                    onChange={handleInputChange}
                />
            
                
                <input
                    type='text'
                    name='descripcion'
                    className='form-control mt-3'
                    placeholder='Descripcion'
                    autoComplete='off'
                    value={descripcion}
                    onChange={handleInputChange}
                />
                 <input
                    type='number'
                    name='monto'
                    className='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    value={monto}
                    onChange={handleInputChange}
                />
                

                <button 
                className='btn btn-primary form-control  mt-2'
                type='submit'  
                
                >
                Ingresar

                </button>

            </div>
        </form>

    </div>
  )
}
