import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getFacSocio } from '../../actions/factura';
import { PdfScreen } from '../Pdf/PdfScreen';

export const TablaFacSocio = () => {

    const {facSocio} = useSelector(state=> state.factura)

    const {id} = facSocio

    const dispatch = useDispatch()

    useEffect(() => {

        let isApiSubscribed =true;
        if(isApiSubscribed){
            dispatch(getFacSocio(id));
        }
         return () => {
            isApiSubscribed = false;
         }
       }, [dispatch])


  return (
    <>
    <table className='table table-striped table-hover'>
        <thead>
            <tr>
                <th>Fecha</th>
                <th>Descripcion</th>
                <th>Monto</th>
                <th>Descargar</th>
            </tr>     
        </thead>
        <tbody>
            {facSocio.map(fac => {
                return(
                    <tr  key={fac.cedula}>
                        <td>{fac.fecha.slice(0,10)}</td>
                        <td>{fac.descripcion}</td>
                        <td>{fac.monto}</td> 
                        <td>
                            <PdfScreen factura={fac} />
                        </td>
                                         
                    </tr>
                    
                );
            })

            }

        </tbody>
    
    </table>





</>
  )
}
