import React from 'react'
import { useDispatch } from 'react-redux'
import { addNewSocio } from '../../actions/socio'


import { useForm } from '../../hooks/useForm'


export const FormSocio = () => {


    const dispatch = useDispatch()

    const [ socioValues, handleInputChange ] = useForm({
        cedula:'',
        nombre: '',
        apellidos: '',
        celular: '',
        placa: '',
       
      })
    
      const { cedula, nombre, apellidos, celular, placa } = socioValues
    
       
    
      const handleSumit = (e) => {
    
        e.preventDefault();
    
       dispatch( addNewSocio( socioValues ) )

      }

    

  return (
    <div>

        <form onSubmit={handleSumit} className='formSocio'>
            <div>
                <input
                    type='number'
                    name='cedula'
                    className='form-control'
                    placeholder='Cedula'
                    autoComplete='off'
                    value={cedula}
                    onChange={handleInputChange}
                />
                <input
                    type='text'
                    name='nombre'
                    className='form-control mt-3'
                    placeholder='Nombre'
                    autoComplete='off'
                    value={nombre}
                    onChange={handleInputChange}
                />
                <input
                    type='text'
                    name='apellidos'
                    className='form-control mt-3'
                    placeholder='Apellidos'
                    autoComplete='off'
                    value={apellidos}
                    onChange={handleInputChange}
                />
                 <input
                    type='number'
                    name='celular'
                    className='form-control mt-3'
                    placeholder='Celular'
                    autoComplete='off'
                    value={celular}
                    onChange={handleInputChange}
                />
                <input
                    type='text'
                    name='placa'
                    className='form-control mt-3'
                    placeholder='Placa'
                    autoComplete='off'
                    value={placa}
                    onChange={handleInputChange}
                />

                <button 
                className='btn btn-primary form-control  mt-2'
                type='submit'  
                
                >
                Ingresar

                </button>

            </div>
        </form>

    </div>
  )
}
