import React from 'react'
import { useSelector } from 'react-redux'
import { TablaFacSocio } from './TablaFacSocio'

export const VisualFacSocio = () => {

    const {socio, totalFacturas} = useSelector(state=> state.factura)
   


    const{nombre, apellidos} = socio
  
  return (
    <div className='contenido'>
        <h1>
            {nombre} {apellidos} -------- ${new Intl.NumberFormat('ja-JP').format(totalFacturas.toFixed(2))}
        </h1>
        <hr/>
        
        <TablaFacSocio />
    </div>



  )
}
