import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getSoc, openModalActPre } from '../../actions/modalActPre';
import { getSocios, startDeleteSocio } from '../../actions/socio';
import { EditSocio } from './EditSocio';

export const TableSocio = () => {

    const dispatch = useDispatch()
    const {socios} = useSelector(state=> state.socio)

    const handleGetSocio = (soc) => {
            
        dispatch(openModalActPre())
        dispatch(getSoc(soc));
        
    }

    const handleDelete = (id) => {
        dispatch(startDeleteSocio(id));
        
       dispatch(getSocios());


    }

    useEffect(() => {
        let isApiSubscribed =true;
        if(isApiSubscribed){
            dispatch(getSocios());
        }
         return () => {
            isApiSubscribed = false;
         }
       }, [dispatch])


  return (
    <>
    <table className='table table-striped table-hover'>
        <thead>
            <tr>
                <th>Cedula</th>
                <th>Nombre</th>
                <th>Apellidos</th>
                <th>Celular</th>
                <th>Placa</th>
                <th>Editar</th>
                <th>Eliminar</th>
            </tr>     
        </thead>
        <tbody>
            {socios.map(soc => {
                return(
                    <tr  key={soc.cedula}>
                        <td>{soc.cedula}</td>
                        <td>{soc.nombre}</td>
                        <td>{soc.apellidos}</td>
                        <td>{soc.celular}</td>
                        <td>{soc.placa}</td>

                        <td>
                            <button
                                className='btn btn-secondary'
                                onClick={()=>handleGetSocio(soc)}
                            >
                                Editar
                            </button>
                        </td>

                        <td>
                            <button 
                                className='btn btn-danger'
                                
                                onClick={()=>handleDelete(soc._id)}
                            >
                                Eliminar
                            </button>
                        </td>
                    </tr>
                    
                );
            })

            }

        </tbody>
    
    </table>
    <EditSocio />
</>
  )
}
