import { applyMiddleware, combineReducers, compose, createStore } from "redux";
import thunk from 'redux-thunk'

import { cargaReducer } from "../reducers/cargaReducer";
import { facturaReducer } from "../reducers/facturaReducer";
import { modalActPreReducer } from "../reducers/modalActPreReducer";
import { socioReducer } from "../reducers/socioReducer";
import { usuarioReducer } from "../reducers/usuarioReducer";





const composeEnhancers = (typeof window !== 'undefined' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) || compose;

const reducers = combineReducers({
    carga: cargaReducer,
    usuario: usuarioReducer,
    modal: modalActPreReducer,
    socio: socioReducer,
    factura: facturaReducer
})

export const store = createStore(
    reducers,
    composeEnhancers(
        applyMiddleware(thunk)
    )
)