import { useSelector } from "react-redux";
import { Routes, Route, BrowserRouter } from "react-router-dom";



import { HomeScreen } from "../Components/Home/HomeScreen";
import { VisualFacSocio } from "../Components/VisualizarFacSocio/VisualFacSocio";

import { Carga } from "../helpers/Carga"
import { NavBar } from "../UI/NavBar";
import { AdminPrivate } from "./adminRouts/AdminPrivate";
import { AdminRouts } from "./adminRouts/AdminRouts";

import { Public } from "./publicRouts/Public";
import { PublicRouts } from "./publicRouts/PublicRouts";
import { SocioPrivate } from "./sociosRouts/SocioPrivate";
import { SocioRouts } from "./sociosRouts/SociosRouts";




export const AppRouter = () => {

    const {checking, perfil} = useSelector(state=> state.usuario)
    

    
    return (
        <BrowserRouter >
            {/*(checking)? <NavBarAdmin /> : <NavBar />*/}

            <NavBar />
            
            <Routes>

                <Route path="/" element={<HomeScreen />} />
                <Route path="/facturasSocio" element={<VisualFacSocio />} />

                <Route 
                    path='/public/*'
                    element={
                        <Public checking={checking} perfil={perfil}>
                            <PublicRouts />
                        </Public>
                    }
                
                />

                <Route 
                    path="/admin/*"
                    element={
                        <AdminPrivate checking= {checking} perfil={perfil} >
                            <AdminRouts />
                        </AdminPrivate>
                    }

                />

                <Route 
                    path="/socio/*"
                    element={
                        <SocioPrivate checking= {checking} perfil={perfil} >
                            <SocioRouts />
                        </SocioPrivate>
                    }
                
                />
                
            </Routes>


            <Carga /> 
        </BrowserRouter>
    )
};
