import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getFacturas, startDeleteFactura } from '../../actions/factura';
import { getFac, openModalActPre } from '../../actions/modalActPre';
import { EditFactura } from './EditFactura';


export const TableFacturas = () => {


    const dispatch = useDispatch()
     const {facturas} = useSelector(state=> state.factura)

    const handleGetFactura = (fac) => {
           
        dispatch(openModalActPre())
        dispatch(getFac(fac));
        
        
    }

    const handleDelete = (id) => {
        
        dispatch(startDeleteFactura(id));
        
       dispatch(getFacturas());


    }

    useEffect(() => {

        let isApiSubscribed =true;
        if(isApiSubscribed){
            dispatch(getFacturas());
        }
         return () => {
            isApiSubscribed = false;
         }
       }, [dispatch])
       
  return (
    <>
    <table className='table table-striped table-hover'>
        <thead>
            <tr>
                <th>Nombre</th>
                <th>Fecha</th>
                <th>Descripcion</th>
                <th>Monto</th>
            </tr>     
        </thead>
        <tbody>
            {facturas.map(fac => {
                return(
                    <tr  key={fac._id}>
                        <td>{fac.socio.nombre}</td>
                        <td>{fac.fecha.slice(0,10)}</td>
                        <td>{fac.descripcion}</td>
                        <td>{fac.monto}</td>

                        <td>
                            <button
                                className='btn btn-secondary'
                                onClick={()=>handleGetFactura(fac)}
                            >
                                Editar
                            </button>
                        </td>

                        <td>
                            <button 
                                className='btn btn-danger'
                                
                                onClick={()=>handleDelete(fac._id)}
                            >
                                Eliminar
                            </button>
                        </td>
                    </tr>
                    
                );
            })

            }

        </tbody>
    
    </table>

    <EditFactura />

</>
  )
}
