import { Routes, Route} from "react-router-dom";
import { CuentaSocioScreen } from "../../Components/CuentaSocios/CuentaSocioScreen";


import { SocioScreen } from "../../Components/Socios/SocioScreen";

export const SocioRouts = () => {
  return (
    <div>
        <Routes>
                <Route path="/socios" element={<SocioScreen />} />
                <Route path="/cuentasocios" element={<CuentaSocioScreen />} />
        </Routes>
    </div>
  )
}
