import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { actualizarFactura } from '../../actions/factura';
import { closeModalActPre, getFac, noUpdatedModalActPres, updatedModalActPre } from '../../actions/modalActPre';




export const EditFactura = () => {

    const dispatch = useDispatch()
     const init ={
        _id:'',
        descripcion: '',
        monto: '',
        socio:''
     }

    const {isOpen, factura, actualizado} = useSelector(state => state.modal)
  
    const [facturaValues, setFacturaValues] = useState({
        descripcion: '',
        monto: '',
    })

    const handleInputChange = ({target }) => {

        setFacturaValues({
            ...facturaValues,
            [target.name]: target.value
        });
    }
    const {  descripcion, monto} = facturaValues;



         

    const customStyles = {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    }

    Modal.setAppElement('#root');

    useEffect(() => {
        
        if(factura){
            setFacturaValues({
                ...facturaValues,
                descripcion: factura.descripcion,
                monto: factura.monto
            }); 
            
        }
        if(actualizado){

            dispatch(noUpdatedModalActPres())
            
        }

    }, [factura, dispatch, actualizado])

 
    const handleSumit = (e) => {
        e.preventDefault();
        
        dispatch(actualizarFactura(facturaValues, factura._id));

        dispatch( updatedModalActPre() ) 
       // dispatch(getFac(init))
       closeModal()   
    }


    const closeModal = () => {
        dispatch(getFac(init))
        dispatch(closeModalActPre())

    }

 
  return (

    <Modal
    
    isOpen={isOpen}
    //onAfterOpen={afterOpenModal}
    onRequestClose={closeModal}
    style={customStyles}
    closeTimeoutMS={200}

    className='modal'
    overlayClassName='modal-fondo'

  >
        <h2>Editor de Factura</h2>
        <form onSubmit={handleSumit} >
            <div className='row'>

                    
                <input
                    type='text'
                    name='descripcion'
                    className='form-control mt-3'
                    placeholder='Descripcion'
                    autoComplete='off'
                    value={descripcion}
                    onChange={handleInputChange}
                />
                 <input
                    type='number'
                    name='monto'
                    className='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    value={monto}
                    onChange={handleInputChange}
                />
                    
                
            </div>
            <div className='row mt-3'>
                <button type='submit' className='btn btn-primary'>
                    Actualizar
                </button>
            </div>
        </form>
        <div className='row mt-3'>
            <button className='btn btn-danger' onClick={closeModal}>
                Cancelar
            </button>
        </div>



  </Modal>
  )
}
