import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { closeModalActPre, noUpdatedModalActPres, updatedModalActPre } from '../../actions/modalActPre';
import { actualizarSocio } from '../../actions/socio';


export const EditSocio = () => {

    const dispatch = useDispatch()


    const {isOpen, socio, actualizado} = useSelector(state => state.modal)
    


    const [actividadValues, setActividadValues] = useState({
        _id:'',
        cedula:'',
        nombre: '',
        apellidos: '',
        celular: '',
        placa: '',
    })
    

    
    const handleInputChange = ({target }) => {

        setActividadValues({
            ...actividadValues,
            [target.name]: target.value,
            
        });
    }

    const { cedula,
        nombre,
        apellidos,
        celular,
        placa } = actividadValues;


    const customStyles = {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    }

    Modal.setAppElement('#root');

    useEffect(() => {
        
        if(socio){
            setActividadValues(socio); 
            
        }

        if(actualizado){

            dispatch(noUpdatedModalActPres())
            
        }


    }, [socio, dispatch, actualizado])

 
    const handleSumit = (e) => {
        e.preventDefault();
        
        dispatch(actualizarSocio(actividadValues));

        

        dispatch( updatedModalActPre() ) 
       closeModal()   
    }

    
    

    const closeModal = () => {
        dispatch(closeModalActPre())

    }

 
  return (

    <Modal
    
    isOpen={isOpen}
    //onAfterOpen={afterOpenModal}
    onRequestClose={closeModal}
    style={customStyles}
    closeTimeoutMS={200}

    className='modal'
    overlayClassName='modal-fondo'

  >
        <h2>Editor de actividad</h2>
        <form onSubmit={handleSumit} className='formularioCliente'>
            <div className='row'>
           
            <input
                    type='number'
                    name='cedula'
                    className='form-control'
                    placeholder='Cedula'
                    autoComplete='off'
                    value={cedula}
                    onChange={handleInputChange}
                />
                <input
                    type='text'
                    name='nombre'
                    className='form-control mt-3'
                    placeholder='Nombre'
                    autoComplete='off'
                    value={nombre}
                    onChange={handleInputChange}
                />
                <input
                    type='text'
                    name='apellidos'
                    className='form-control mt-3'
                    placeholder='Apellidos'
                    autoComplete='off'
                    value={apellidos}
                    onChange={handleInputChange}
                />
                 <input
                    type='number'
                    name='celular'
                    className='form-control mt-3'
                    placeholder='Celular'
                    autoComplete='off'
                    value={celular}
                    onChange={handleInputChange}
                />
                <input
                    type='text'
                    name='placa'
                    className='form-control mt-3'
                    placeholder='Placa'
                    autoComplete='off'
                    value={placa}
                    onChange={handleInputChange}
                />
                <div className='row mt-2'>
                    <button type='submit' className='btn btn-primary'>
                        Actualizar
                    </button>
                </div>
                
            </div>
        </form>
        <div className='row mt-2'>
            <button className='btn btn-danger' onClick={closeModal}>
                Cancelar
            </button>
        </div>



  </Modal>
  )
}
