import { types } from "../types/types"


const initialState = {
    socios: [],
    //id: 0
 
}


export const socioReducer = (state = initialState, action ) => {

    switch (action.type) {
        case types.socioAdd:
            return {
                ...state,
                socios: [
                    ...state.socios,
                    action.payload
                ]
            }
       
        case types.socioGets:
            return{
                ...state,
                socios: [ ...action.payload ]
            }
/* 
        case types.idActividad:
            return{
                ...state,
                id: action.payload
            }
    */   
        default:
            return state
            break;
    }
}