import React from 'react';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import { numerosaLetras } from '../../helpers/numerosLetras';
import Swal from 'sweetalert2';


export const PdfScreen = ({factura}) => {

  const {fecha, descripcion, monto} = factura

  const {cedula, nombre, apellidos} = factura.socio

  const montoLetras = numerosaLetras(monto);

  
  const datosCliente = [
    `Nombre: ${nombre} ${apellidos}`,
    `Fecha: ${fecha.slice(0,10)}`

  ]


  const piePresupuesto = [
    '________________________________________',
    'FERNEY ANDRES DIAZ ROMERO',
    'TESORERO'
  ]




  const generatePDF = () => {

    var doc = new jsPDF('p', 'mm','letter')
    
    
    const logoData = 'data:image/png;base64,';



   /*
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  */


    const firma = 'data:image/png;base64,';

    

    //////Presupuesto////////////////////////////////////

    // Encabezado ////////

    doc.addImage(logoData, 'JPG', 20,10,70,30)

    doc.roundedRect(100,10,100,32,3,3)
    doc.setFontSize(14)
    doc.setFont('times', 'bold')
    doc.text(`FECHA: ${fecha.slice(0,10)}`, 130,28, {maxWidth:95} )
    
    ///////////////////////////////

    doc.setFontSize(16)
    doc.setFont('times', 'bold')
    doc.text('RECIBO DE CAJA',80,60)

    doc.setFontSize(12)
    doc.setFont('times', 'normal')
    doc.text(datosCliente, 20,73)

  
    doc.setFontSize(12)
    //doc.text(`Total presupuesto      $${new Intl.NumberFormat('ja-JP').format(total.toFixed(2))}`, 20, 180, {maxWidth:180})
    doc.text(`Recibi del seor(ar) ${nombre} ${apellidos} identificado con cedula de ciudadania ${cedula} por concepto de ${descripcion} la suma de ${montoLetras} ($${new Intl.NumberFormat('ja-JP').format(monto.toFixed(2))})  `, 20,100, {maxWidth:180})
    doc.text(piePresupuesto, 20,150)
    doc.addImage(firma, 'PNG', 20,130,60,30)
    
   

    if(nombre){

      doc.save(`${nombre} ${apellidos}.pdf`)
    }else{
      Swal.fire({
        title: 'Error!',
        text: `No es permitido`,
        icon: 'error',
        confirmButtonText: 'Cool'
      })
    }
    

 
  }      
  
 //render() 
 
    return (
       <div>
          <button 
            className='btn btn-info'
            onClick={generatePDF}
          >
            Descargar PDF
          </button> 
       </div>
    )
 

    
 
}







