import { fetchSinToken } from "../helpers/fetch"
import { types } from "../types/types"
import { Cargado, Cargando } from "./carga"
import Swal from "sweetalert2";

export const addNewSocio = ( socio ) => {

    return async ( dispatch ) => {
        try {
            dispatch( Cargando() )


            const respuesta = await fetchSinToken('socio/new', socio , 'POST')
            const body = await respuesta.json();
            dispatch(Cargado())
            if(body.ok){
                const Toast = Swal.mixin({
                    toast: true,
                    position: 'top-end',
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                      toast.addEventListener('mouseenter', Swal.stopTimer)
                      toast.addEventListener('mouseleave', Swal.resumeTimer)
                    }
                  })

                  
                  
                  Toast.fire({
                    icon: 'success',
                    title: 'Registro Exitoso'
                  })
                dispatch(addSocio(socio))
                dispatch(getSocios())               
          
 
            }else{

                Swal.fire({
                title: 'Error!',
                text: `${body.msg}`,
                icon: 'error',
                confirmButtonText: 'Cool'
                })    
            }
        } catch (error) {
            console.log(error)
        }
    }

}


const addSocio = ( socio ) => ({
    type: types.socioAdd,
    payload: socio
})




export const getSocios = () => {
    return async ( dispatch ) => {
        try {
            dispatch( Cargando() )
            const respuesta = await fetchSinToken('socio')
            const body = await respuesta.json();
            dispatch(Cargado())
            if(body.ok){

                dispatch(Socios(body.socios))
            }

        } catch (error) {
            console.log(error)
            
        }
    }
}

const Socios = ( socios ) => ({
    type: types.socioGets,
    payload: socios
})


export const startDeleteSocio = (id) =>{

    return  (dispatch) => {

        const swalWithBootstrapButtons = Swal.mixin({
            customClass: {
              confirmButton: 'btn btn-success',
              cancelButton: 'btn btn-danger'
            },
            buttonsStyling: false
          })
          
          swalWithBootstrapButtons.fire({
            title: ' Estas seguro?',
            text: "Si eliminas no podras recuperar la informacion!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Si, Eliminar!',
            cancelButtonText: 'No, cancelar!',
            reverseButtons: true
          }).then((result) => {
            if (result.isConfirmed) {

                   dispatch(eliminado(id))

                swalWithBootstrapButtons.fire(
                    'Eliminado!',
                    'Su registro ha sido eliminado',
                    'success'
                )
                    
                    
 
            } else if (
              /* Read more about handling dismissals below */
              result.dismiss === Swal.DismissReason.cancel
            ) {
                swalWithBootstrapButtons.fire(
                    'Cancelado',
                    'Sus registros siguen intactos :)',
                'error'
              )
            }
        })
        
    }
}

const eliminado = (id) =>{

    return async (dispatch) => {
        try {
            const respuesta = await fetchSinToken( `socio/${id}`,{},'DELETE');
            const body = await respuesta.json()
            if(body.ok){
                dispatch(getSocios());
            }
        } catch (error) {
            console.log(error)    
        }
    }
}

export const actualizarSocio = (socio ) => {
  
    return async (dispatch) => {
      dispatch(Cargando())
      try {
  
        const respuesta = await fetchSinToken(`socio/${socio._id}`,socio, 'PUT')
        const body = await respuesta.json();
        dispatch(Cargado())
        
        if(body.ok){
          await dispatch(getSocios());
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Ha sido actualizado',
            showConfirmButton: false,
            timer: 1000
          })
        }
  
         
      } catch (error) {
  
        Swal.fire({
                  title: 'Error!',
                  text: `${error}`,
                  icon: 'error',
                  confirmButtonText: 'Cool'
                })
      }
    }
  }