import React from 'react'
import { FormSocio } from './FormSocio'
import { TableSocio } from './TableSocio'


export const SocioScreen = () => {
  return (
    <div className='contenido'>
        <h1>SOCIOS</h1>
        <hr/>
        
        <div className='row row-cols-1 row-cols-md-2 mt-5'>
            <div className='col'>

                <FormSocio />
            </div>
            <div className='col'>
                <TableSocio />
            </div>
        </div>
    </div>
  )
}
