import { types } from "../types/types"

const initialState = {
    facturas: [],
    cuentaSocios:[],
    total: 0,
    facSocio:[],
    socio:{},
    totalFacturas:0
    //id: 0
 
}

export const facturaReducer = (state = initialState, action ) => {
    switch (action.type) {
        case types.facturaAdd:
            return {
                ...state,
                facturas: [
                    ...state.facturas,
                    action.payload
                ]
            }
       
        case types.facturaGets:
            return{
                ...state,
                facturas: [ ...action.payload ]
            }
        case types.facturaCuentaSocios:
            return{
                ...state,
                cuentaSocios: [ ...action.payload.cuentasSocios ],
                total: action.payload.total
            }
        case types.facturasSocio:
            return{
                ...state,
                facSocio: [ ...action.payload.facturas ],
                socio: action.payload.socio,
                totalFacturas: action.payload.total
            }
/* 
        case types.idActividad:
            return{
                ...state,
                id: action.payload
            }
    */   
        default:
            return state
            break
    }
}
