
import { Navigate } from 'react-router-dom'

export const Public = ({checking, perfil, children}) => {

  

    if(checking && perfil==='admin'){
       return( <Navigate to='/admin/socios' /> )
    }else if(checking && perfil==='socio'){
       return ( <Navigate to='/socio/socios' /> )
    }else{
      return(  children )
    }
    
}
