import React from 'react'
import { FormFactura } from './FormFactura'
import { TableFacturas } from './TableFacturas'

export const FacturaScreen = () => {
  return (
    <div className='contenido'>
      <h1>Facturas</h1>
      <hr/>
      <div className='row row-cols-1 row-cols-md-2 mt-5'>

        <div className='col'>
          <FormFactura />
        </div>
        <div className='col'>
          <TableFacturas />

        </div>
      </div>

    </div>
  )
}
