import Swal from "sweetalert2";
import { fetchConToken, fetchSinToken } from "../helpers/fetch"
import { types } from "../types/types";
import { Cargado, Cargando } from "./carga";

export const startLogin = ({usuario, password}) => {
    return async(dispatch) =>{
        try {
            const respuesta = await fetchSinToken('usuario/login', { usuario, password }, 'POST');
            const body = await respuesta.json();
    
            if(body.ok){
                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());
                dispatch(login({
                    uid: body.uid,
                    perfil: body.perfil
                }))
            }else {
                Swal.fire('Error', body.msg, 'error')
            }
            
        } catch (error) {
            console.log(error)
        }

       

    }
}

export const login = ( usuario ) => ({
    type: types.usuarioLogin,
    payload: usuario

})

export const startChecking = () => {
    return async (dispatch) =>{
        try {
            const respuesta = await fetchConToken('login/renew');
            const body = await respuesta.json();

            if(body.ok){
                localStorage.setItem('token', body.token);
                localStorage.setItem('token-init-date', new Date().getTime());
                dispatch(login({
                    uid: body.uid
                }))
            }else {
                dispatch(checkingFinish())
            }

        }catch (error) {
            console.log(error)           
                
        }   
    }

        
}

export const checkingFinish = () => ({ type: types.usuarioChekingFinish})