import React from 'react'
import './styleNav.css'

import { Link, NavLink } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { checkingFinish } from '../actions/usuario'


export const NavBar = () => {

    const dispatch = useDispatch()
    const Logout = () => {
        dispatch(checkingFinish())
    }

    const {checking, perfil} = useSelector(state => state.usuario)


    //##########  Administrador ###################//
    if(checking && perfil==='admin') return(
        <nav>
        <div className='li'>
            <Link 
                className='a'
                to='/'
            >
                Inicio
            </Link>
        </div>
         <div className='li'>
            <NavLink 
                className='a'
                to='admin/socios'
            >
                Socios
            </NavLink>
        </div>
         <div className='li'   >
            <NavLink  
                className='a'
                to='admin/cuentasocios'
            >
                Cuentas Socios
            </NavLink>  
        </div>
        
        <div className='li'   >
            <NavLink  
                className='a'
                to='admin/facturas'
            >
                Facturas
            </NavLink>  
        </div>


        <div className='li'>
            <NavLink 
            
                className='a'
                onClick={Logout}
                
                to="/"
            >
                Logout
            </NavLink>
        </div>
        
        
    </nav>
    )

//##########  Socios ###################//

    if(checking && perfil==='socio') return(
        <nav>
        <div className='li'>
            <Link 
                className='a'
                to='/'
            >
                Inicio
            </Link>
        </div>
         <div className='li'>
            <NavLink 
                className='a'
                to='socio/socios'
            >
                Socios
            </NavLink>
        </div>
        <div className='li'   >
            <NavLink  
                className='a'
                to='socio/cuentasocios'
            >
                Cuentas Socios
            </NavLink>  
        </div>
         
        <div className='li'>
            <NavLink 
            
                className='a'
                onClick={Logout}
                
                to="/"
            >
                Logout
            </NavLink>
        </div>
        
        
    </nav>
    )


//##########  Publico  ###################//
  return (
    <nav>
        <div className='li'>
            <Link 
                className='a'
                to='/'
            >
                Inicio
            </Link>
        </div>
        
        <div className='li'>
            <NavLink
                className='a'
                to='public/login'
            >
                Ingresar

            </NavLink>
            
        </div>
        
    </nav>
  )
}
