import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { getCuentasSocios, getFacSocio } from '../../actions/factura';

export const TablaCuentaSocios = () => {

    const dispatch = useDispatch()
    const {cuentaSocios, total} = useSelector(state=> state.factura)

     const getFac = (id) => {

        dispatch(getFacSocio(id))

     }
     

    useEffect(() => {

        let isApiSubscribed =true;
        if(isApiSubscribed){
            dispatch(getCuentasSocios());
        }
         return () => {
            isApiSubscribed = false;
         }
       }, [dispatch])

  return (
    
        <>
        <table className='table table-striped table-hover'>
            <thead>
                <tr>
                    <th>Nombre</th>
                    <th>Monto</th>
                    <th>Ver</th>
                </tr>     
            </thead>
            <tbody>
                {cuentaSocios.map(fac => {
                    return(
                        <tr  key={fac.cedula}>
                            <td>{fac.nombre}</td>
                            <td>{fac.monto}</td> 
                            <td>
                                <Link
                                    className='btn btn-info'
                                    to={`/facturasSocio`}
                                    onClick={()=> getFac(fac.id)}
                                   
                                >
                                    Ver
                                </Link>
                            </td>                       
                        </tr>
                        
                    );
                })

                }

            </tbody>
        
        </table>

        <h3>Total: {total}</h3>



    </>



  )
}


    
       

  
