import { types } from "../types/types"

const initialState = {

    isOpen: false,
    socio:{
        _id:'',
        cedula:'',
        nombre: '',
        apellidos: '',
        celular: '',
        placa: '',
    },
    factura:{
        _id: '',
        descripcion: '',
        monto: 0,
        socio: ''
    },
    actualizado: false
}

export const modalActPreReducer = (state = initialState, action) => {

    switch (action.type) {
        case types.modalOpen:
            return {
                ...state,
                isOpen: true
            }
        case types.modalCLose:
            return{
                ...state,
                isOpen: false
            }  
        case types.modalSocio: return{
            ...state,
            socio: action.payload
        }

        case types.modalFactura: return{
            ...state,
            factura: {
                _id: action.payload._id,
                descripcion: action.payload.descripcion,
                monto: action.payload.monto,
                socio: action.payload.socio._id
            }
        }
        
        case types.modalActualizado:
            return {
                ...state,
                actualizado: true
            }
        case types.modalNoActuliazado:
            return {
                ...state,
                actualizado: false
            }
    
        default:
            return state
    }




}